.section-offset {
  padding: 30px 0;

  @include big-tablet {
    padding: 22.5px 0;
  }

  @media (max-width: 640px) {
    padding: 10px 0;
  }
}

.section-title {
  position: relative;
  margin: 0;

  font-size: 20px;
  line-height: 29px;
  text-transform: uppercase;
  color: var(--black-color);
  font-weight: 700;
  text-align: center;

  &::after {
    content: "";
    position: absolute;
    right: 50%;
    left: 50%;
    bottom: -10px;
    transform: translate(-50%, -50%);
    opacity: 0.4;
    width: 66px;
    height: 3px;
    background-color: var(--green-color);
  }
}

.article__title {
  margin: 0;
}

.article__title-link {
  font-size: clamp(1rem, 0.9403rem + 0.2581vw, 1.25rem);
  text-transform: uppercase;
  line-height: 24px;
  font-weight: 700;
  transition: 0.3s ease-in-out;

  @include hover {
    color: var(--green-color);
  }

  @media (max-width: 640px) {
    line-height: 20px;
  }
}

.article__title-black {
  color: var(--black-color);
}

.article__title-white {
  color: var(--light-color);
}

.product__title {
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--black-color);
  font-weight: 700;
}

.article__link-category {
  font-size: 14px;
  text-transform: uppercase;
  color: var(--green-color);
  font-weight: 700;

  @include hover {
    text-decoration: underline;
  }
}

.article__list {
  display: flex;
  gap: 20px;
}
