.production {
  &__container {
  }

  &__title {
  }

  &__list {
    margin-top: 45px;

    @include flex-all-sb;
    gap: 20px;
  }

  &__item {
    max-width: 345px;
  }

  &__article {
  }

  &__product {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.product {
  &__image {
  }

  &__text {
    margin-top: -25px;
    padding: 35px;
    border: 3px solid #b59f5b;
  }

  &__title {
    margin: 0;
  }

  &__descr {
    margin: 0;
  }

  &__bottom {
    margin-top: 25px;

    display: flex;
    justify-content: space-between;
  }

  &__money {
    display: flex;
    flex-direction: column;
  }

  &__price-old {
    font-size: 14px;
    text-decoration: line-through;
    text-transform: uppercase;
    color: #0f0d0e;
    font-weight: 700;
  }

  &__price {
    font-size: 20px;
    text-transform: uppercase;
    color: #0f0d0e;
    font-weight: 700;
  }
}

.button {
  &__purchase {
    padding: 5px 10px;

    font-size: 14px;
    line-height: 40px;
    text-transform: uppercase;
    color: #ffffff;
    background-color: var(--green-color);
    font-weight: 700;
  }
}
